import connect from "@vkontakte/vk-connect";
import {getAllStartParams} from "./StartParams";


export function shareStory() {
  const resultWrap = document.querySelector('.StoryShare__content');
  const base64 = 'data:image/png;base64,';

  const sticker = {
    "sticker_type": "renderable",
    "sticker": {
      "content_type": "image",
      "blob": base64,
      "transform": {
        "relation_width": 0.75
      },
      "clickable_zones": [
        {
          "action_type": "app",
          "action": {
            app_id: +getAllStartParams().appId
          }
        },
      ],
      "can_delete": false,
    }
  };
  const stickers = [sticker];
  const params = {
    background_type: 'none',
    locked: false,
    stickers
  };

  connect.send('VKWebAppShowStoryBox', params)
    .then(data => {
      resultWrap.textContent = JSON.stringify(data)
    })
    .catch(err => {
      resultWrap.textContent = JSON.stringify(err)
    })
}